import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Typography, Container, FormControl, InputLabel, Select, MenuItem } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import { Responsive } from 'semantic-ui-react'
import PropTypes from 'prop-types'
import yearbookSelector from '../../redux/selector/yearbookSelector'
import ACTION from '../../redux/actions/yearbookAction'
import styles from './styles'

export class YearbookPage extends Component {
  static propTypes = {
    generation: PropTypes.array.isRequired,
    list: PropTypes.object.isRequired,
    getContent: PropTypes.func.isRequired,
    classes: PropTypes.object.isRequired,
  }

  state = {
    selectedGen: '',
  }

  onChangeGen = event => this.setState({ selectedGen: event.target.value })

  componentDidMount() {
    this.props.getContent()
  }

  renderProfile = item => {
    const { classes } = this.props
    return (
      <div key={item.name}>
        <Responsive {...Responsive.onlyMobile}>
          <img src={item.imageUrl} alt={item.name} className={classes.profileImage} />
          <Typography>{item.nickname}</Typography>
          <Typography>{item.name}</Typography>
          <Typography>{item.studentID}</Typography>
          <Typography>{item.major}</Typography>
        </Responsive>
      </div>
    )
  }

  render() {
    const { classes, generation, list } = this.props
    const { selectedGen } = this.state
    const currentSelect = selectedGen || generation[0] || ''
    return (
      <Container style={{ paddingTop: '4em' }}>
        <Typography variant="h5">YEARBOOK PAGE HERREEEE</Typography>
        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="age-simple">รุ่นที่</InputLabel>
          <Select
            value={currentSelect}
            onChange={this.onChangeGen}
            inputProps={{
              name: 'age',
              id: 'age-simple',
            }}
          >
            {generation.map(item => (
              <MenuItem key={item} value={item}>
                รุ่นที่ {item - 48}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <div>{currentSelect && list[currentSelect].map(item => this.renderProfile(item))}</div>
      </Container>
    )
  }
}

const mapStateToProps = state => yearbookSelector(state)

const mapDispatchToProps = {
  getContent: ACTION.getContent,
}

const styleConnector = withStyles(styles)(YearbookPage)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(styleConnector)
